
.map {
  margin-bottom: 30px;
  margin-top: 20px;
}
.map .map-inner {
  width: 1012px;
  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 3px;
  margin: 0 auto;
  margin-top: 10px;
}
.map .map-inner .block-box {
  margin-top: 25px;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 10px;
  width: 925px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.map .map-inner .block-box .week {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.map .map-inner .block-box .week .wday {
  font-size: 9px;
  color: #767676;
  line-height: 17px;
  margin-top: 17px;
}
.map .map-inner .block-box .block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 119px;
  width: 901px;
  margin-left: 10px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.map .map-inner .block-box .block .item {
  height: 17px;
  width: 17px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #ebedf0;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  cursor: pointer;
}
.map .map-inner .block-box .block .item .month {
  position: relative;
  top: -25px;
  height: 14px;
  line-height: 14px;
  font-size: 10px;
  color: #767676;
}
.map .map-inner .legend {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  float: right;
  margin-right: 30px;
  margin-bottom: 20px;
}
.map .map-inner .legend .less,
.map .map-inner .legend .more {
  height: 15px;
  line-height: 12px;
  font-size: 12px;
  color: #767676;
}
.map .map-inner .legend .item-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 10px;
}
.map .map-inner .legend .item-box .item {
  width: 15px;
  height: 15px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background: green;
  margin: 0 2px;
}
.map .map-inner .legend .item-box .item:nth-child(1) {
  background: #c6e48b;
}
.map .map-inner .legend .item-box .item:nth-child(2) {
  background: #7bc96f;
}
.map .map-inner .legend .item-box .item:nth-child(3) {
  background: #239a3b;
}
.map .map-inner .legend .item-box .item:nth-child(4) {
  background: #196127;
}
.map .map-inner .allLog {
  font-size: 14px;
  line-height: 12px;
  margin-left: 70px;
}
.map .map-inner .allLog a {
  text-decoration: none;
  color: #0366d6;
}



