
.articleList {
  width: 1012px;
  margin: 0 auto;
}
.articleList ul {
  padding-left: 0px;
}
.articleList ul li {
  list-style: none;
  border-bottom: 1px solid rgba(27, 31, 35, 0.15);
  padding: 20px 0;
  padding-right: 30%;
}
.articleList ul li h5 {
  font-weight: 600;
  cursor: pointer;
  color: #0366d6;
}
.articleList ul li .desc {
  font-size: 13px;
  color: #587000;
  margin: 20px 0;
}
.articleList ul li .footer {
  font-size: 12px;
  color: #222;
}
.articleList ul li .footer .createTime {
  margin-left: 10px;
  font-size: 14px;
}
.articleList .loading {
  height: 50px;
  line-height: 50px;
  text-align: center;
}



